import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {

    getMedicamentos(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('servicio-salud/farmacia/producto', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
