import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {

    fetchMedicamento(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .get('servicio-salud/farmacia/producto', { params })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    addMedicamentoReceta(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .post(`/servicio-salud/atencion/${params.atencion_id}/receta`, params)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    removeMedicamentoReceta(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`servicio-salud/atencion/${params.idAtencion}/receta/${params.idMedicamentoReceta}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    updateMedicamentoReceta(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .put(`servicio-salud/atencion/${params.atencion_id}/receta/${params.medicamento_id}`, params)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
